html {
    height: 100%;
    width: 100%;
}

body {
    margin: 0;
    padding: 0;
    min-height: 100%;
    font-family: -apple-system, "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@media all and (min-width: 950px) {
    .masthead-card-padding {
        margin-left: 20vh;
    }

    .desktop-masthead-content {
        height: 100vh;
    }
    .roadmap-card {
        max-width: 16.6% !important;
    }
}

@media all and (max-width: 949px) {
    .masthead-card-padding {
        margin: 10vh;
    }

    .mobile-masthead {
        height: 50vmax;
        z-index: 1;
    }

    .mobile-masthead .mobile-masthead-content {
        height: 50vmax;
        z-index: 1;
    }
}

.masthead {
    height: 100vh;
    z-index: 1;
    padding: 0 !important;
}

.masthead .content {
    background: url(../images/masthead.png) no-repeat center;
    background-size: cover;
    height: 100vh;
}

.menu-container {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
}

.menu {
    height: 90px;
}

.menu-short {
    height: 72px;
    margin-top: -12px !important;
}

.menu-right-items {
    padding: 0 !important;
    margin-right: 20px !important;
}

.dark-background {
    background-color: #243038 !important;
}

.grow-up-animation {
    animation: grow-up 2s ease-in-out 0s;
    animation-fill-mode: forwards;
}

.fade-in-animation {
    animation: fade-in 0.8s ease-in-out 0s;
    animation-fill-mode: forwards;
}

.email {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    border-radius: 8px;
    padding: 4px;
}

.email-form {
    max-width: 80% !important;
    background-color: rgba(255, 255, 255, 0.7);
    height: 53px;
}

.menu-email-form {
    background-color: rgba(255, 255, 255, 0.89);
    border-radius: 8px;
    min-width: 35vmin;
    /*margin-top: 4px !important;*/
    border: none;
}

.email-input {
    flex-grow: 2 !important;
    padding-left: 10px;
    border: none;
}

.mobile-email-input {
    margin-top: 15vmin;
    opacity: 0.70;
}

.navbar {
    padding-bottom: 0 !important;
}

.roadmap-content {
    padding: 0 !important;
    margin-bottom: -5px !important;
    flex-grow: unset !important;
}

.roadmap-extra-content {
    margin-top: 16px;
}

.ui.card > .extra, .ui.cards > .card > .extra {
    border-top: 1px solid #C011AF !important;
    /* border-image: linear-gradient(to bottom right, #8E1AAD 0%, #C011AF 75%, #BD10E0 100%); */
    /* border-image-slice: 1; */
    /*border-top: 1px solid !important;*/
}

.inline {
    display: inline;
}

.roadmap-title {
    height: 57px;
    width: 202px;
    color: #243038;
    font-size: 48px;
    font-weight: bold;
    letter-spacing: -0.5px;
    line-height: 57px;
}

.roadmap-card {
    margin: 0 !important;
    box-shadow: none !important;
}

.roadmap-header {
    display: inline;
    margin-right: 8px;
    z-index: 30;
}

.roadmap-content {
    display: inline-block;
    vertical-align: top; /* here */
}

.software-is-sometimes {
    height: auto;
    width: 100%;
    color: #243038;
    font-size: 16px;
    font-weight: 300;
    line-height: 26px;
}
