#Opportunity {
  background: #f9f9f9;
  position: relative;
  font-family: roboto, sans-serif;
  padding: '20rem';
  height: 100vh;
  height: auto;
  z-index: -1;
  margin: '20em';
}

#card {
  margin: 0;
  width: 100%;
  border-radius: 0;
  box-shadow: 0 2.5px 5px #bbbbbb;
}

#card > .card-wrapper > h3 {
  font-size: 1.5rem !important;
  letter-spacing: -0.05rem;
  margin-bottom: 0.5rem;
}

#card > .card-wrapper > p {
  font-size: 1.125rem !important;
  font-weight: 100;
  line-height: 1.35rem;
  /* letter-spacing: -0.05rem; */
}

#card > .card-wrapper > p > b {
  font-weight: 500;
}

.card-wrapper {
  margin: 2.75rem;
}

#card-col {
  padding: 0 !important;
}

#image-wrapper {
  height: 100%;
}