@-webkit-keyframes grow-up {
    0% {
        opacity: 0;
        height: 0;
    }
    100% {
        opacity: 1;
        height: 100%;
    }
}

@keyframes grow-up {
    0% {
        opacity: 0;
        height: 0;
    }
    100% {
        opacity: 1;
        height: 100%;
    }
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

html {
    height: 100%;
    width: 100%;
}

body {
    margin: 0;
    padding: 0;
    min-height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.splash-page-background {
    display: flex;
    height: 100vh;
    background-image: url(/static/media/coming-soon-background.b4b2176b.jpg);
    background-size: cover;
    opacity: 0.89;
}

.splash-page-unsplash-a {
    position: absolute;
    bottom: 10px;
    right: 10px;
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 4px 6px;
    /*font-family: -apple-system, BlinkMacSystemFont, & quot San Francisco & quot;, & quot Helvetica Neue & quot;, Helvetica, Ubuntu, Roboto, Noto, & quot Segoe UI & quot, Arial, sans-serif;*/
    font-size: 12px;
    font-weight: bold;
    line-height: 1.2;
    display: inline-block;
    border-radius: 3px
}

.splash-page-unsplash-span {
    display: inline-block;
    padding: 2px 3px
}

.splash-page-unsplash-svg {
    height: 12px;
    width: auto;
    position: relative;
    vertical-align: middle;
    top: -1px;
    fill: white
}

.splash-page-unsplash-source {
    display: inline-block;
    padding: 2px 3px
}

html {
    height: 100%;
    width: 100%;
}

body {
    margin: 0;
