@media all and (min-width: 950px) {
    .masthead-card-padding {
        margin-left: 20vh;
    }

    .desktop-masthead-content {
        height: 100vh;
    }
    .roadmap-card {
        max-width: 16.6% !important;
    }
}

@media all and (max-width: 949px) {
    .masthead-card-padding {
        margin: 10vh;
    }

    .mobile-masthead {
        height: 50vmax;
        z-index: 1;
    }

    .mobile-masthead .mobile-masthead-content {
        height: 50vmax;
        z-index: 1;
    }
}

.masthead {
    height: 100vh;
    z-index: 1;
    padding: 0 !important;
}

.masthead .content {
    background: url(/static/media/masthead.e5843d13.png) no-repeat center;
    background-size: cover;
    height: 100vh;
}

.menu-container {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
}

.menu {
    height: 90px;
}

.menu-short {
    height: 72px;
    margin-top: -12px !important;
}

.menu-right-items {
    padding: 0 !important;
    margin-right: 20px !important;
}

.dark-background {
    background-color: #243038 !important;
}

.grow-up-animation {
    -webkit-animation: grow-up 2s ease-in-out 0s;
            animation: grow-up 2s ease-in-out 0s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.fade-in-animation {
    -webkit-animation: fade-in 0.8s ease-in-out 0s;
            animation: fade-in 0.8s ease-in-out 0s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.email {
