}

.email-form {
    max-width: 80% !important;
    background-color: rgba(255, 255, 255, 0.7);
    height: 53px;
}

.menu-email-form {
    background-color: rgba(255, 255, 255, 0.89);
    border-radius: 8px;
    min-width: 35vmin;
    /*margin-top: 4px !important;*/
    border: none;
}

.email-input {
    flex-grow: 2 !important;
    padding-left: 10px;
    border: none;
}

.mobile-email-input {
    margin-top: 15vmin;
    opacity: 0.70;
}

.navbar {
